import Vue from 'vue'

export default {
  SET_SALES_ORDERS (state, payload) {
    Vue.set(state.list, 'data', payload.results)
    Vue.set(state.list, 'count', payload.count)
    state.current = payload[0]
  },
  PUSH_SALES_ORDER (state, payload) {
    state.list.data.unshift(payload)
  },
  SET_ERROR (state, error) {
    state.error = error
  }
}



