export default {
  authenticated (state, getters) {
    return (state.refreshToken !== undefined && state.authenticated)
  },
  authToken (state, getters) {
    return state
  },
  getAccessToken (state, getters) {
    return state.accessToken
  },
  getExpireTime (state) {
    return new Date(state.expireAt)
  },
  getRefreshToken (state) {
    return state.refreshToken
  }
}



