import { VendorsView, VendorCreateForm, VendorsListView } from './components'

export default [
  {
    path: '/vendors',
    components: {
      default: VendorsView
    },
    meta: { requiredAuth: true, title: 'Vendors' },
    children: [
      {
        path: 'create',
        name: 'vendors.create',
        component: VendorCreateForm,
        meta: { requiredAuth: true, title: 'Vendors > Add' }
      },
      {
        path: 'list',
        name: 'vendors.list',
        component: VendorsListView,
        meta: { requiredAuth: true, title: 'Vendors > List' }
      },
      {
        path: '',
        redirect: 'list',
        name: 'vendorsView'
      }
    ]
  }
]



