// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import {App} from './apps'
import router from './router'
import store from './store'
import Toastr from 'vue-toastr'
// import { reInitiateForm } from './helpers'

require('vue-toastr/src/vue-toastr.scss')

const MDL = () => import(/* webpackChunkName: "mdl" */ 'material-design-lite')
const VEE_VALIDATE = () => import(/* webpackChunkName: "vee-validate" */ 'vee-validate').then((VeeValidate) => {
  Vue.use(VeeValidate)
})

/* eslint-disable no-new */
Vue.config.productionTip = false
// Vue.mixin({
//   updated () {
//     reInitiateForm()
//   }
// })

MDL().then(() => {
  Vue.component('vue-toastr', Toastr)
  Vue.use(Toastr)
  return VEE_VALIDATE()
}).then(() => {
  return new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>'
  }).$mount('#app')
})



