<template>
  <div id="app">
    <dashboard-layout v-if="authenticated">
      <div slot="content">
        <router-view></router-view>
      </div>
    </dashboard-layout>
    <router-view v-else></router-view>
  </div>
</template>
<script>
import { DashboardLayout } from 'LAYOUTS_COMPONENTS'
import { mapActions, mapGetters } from 'vuex'

export default {
  watch: {
    authenticated (value) {
      if (!value) {
        this.$router.replace({ name: 'user.login' })
      } else {
        this.$router.replace({ name: 'user.dashboardView' })
      }
    }
  },
  components: {
    DashboardLayout
  },
  beforeMount () {
    // this.loadOrganizationsFromStorage()
  },
  computed: {
    ...mapGetters([
      'authenticated'
    ])
  },
  methods: {
    ...mapActions([
      'loadOrganizationsFromStorage'
    ])
  }
}
</script>

<style lang="scss">
  @import url('https://fonts.googleapis.com/icon?family=Material+Icons');
  @import url('https://code.getmdl.io/1.2.1/material.indigo-pink.min.css');
  @import '~SCSS/style.scss';
</style>



