import * as actions from './actions'
import getters from './getters'
import mutations from './mutations'

const state = {
  list: {
    data: [],
    count: 0
  },
  current: {
    id: undefined,
    name: undefined,
    quantity: 0,
    is_active: undefined
  },
  error: '',
  orders: {
    data: [],
    count: 0
  }
}

export default {
  state,
  actions,
  getters,
  mutations
}



