import { Login, DashboardView, UserView } from './components'

export default [
  {
    path: '/user',
    name: 'user',
    components: {
      default: UserView
    },
    children: [
      {
        path: 'login',
        component: Login,
        name: 'user.login'
      },
      {
        path: 'dashboard',
        component: DashboardView,
        meta: { requiredAuth: true, title: 'Dashboard' },
        name: 'user.dashboardView'
      }
    ]
  }
]



