<script>
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'auth',
    methods: {
      ...mapActions([
        'login',
        'refreshOauthToken',
        'userLogout',
        'companyLogout',
        'resetAuthError'
      ]),
      logout () {
        this.userLogout().then(() => {
          this.$nextTick(() => {
            this.$router.replace({ name: 'user.login' })
          })
        }).catch(e => {
          this.$toastr.e(e)
        })
      }
    },
    computed: {
      ...mapGetters({
        authenticated: 'authenticated',
        authToken: 'authToken'
      })
    },
    beforeMount () {
      // this.resetAuthError()
    }
  }
</script>



