import { getProcurements, postProcurement } from '../api'
import qs from 'qs'

export const fetchProcurements = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return getProcurements(params).then(response => {
    commit('SET_PROCUREMENTS', response.data)
  })
}

export const addProcurement = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return postProcurement(params).then(response => {
    commit('PUSH_PROCUREMENT', response.data)
  })
}



