import Vue from 'vue'

export default {
  SET_RAWMATERIALS (state, payload) {
    Vue.set(state.list, 'data', payload.results)
    Vue.set(state.list, 'count', payload.count)
    state.current = payload[0]
  },
  PUSH_RAWMATERIAL (state, payload) {
    state.list.push(payload)
  },
  SET_ERROR (state, error) {
    state.error = error
  },
  SET_PURCHASE_ORDERS (state, payload) {
    Vue.set(state.orders, 'data', payload.results)
    Vue.set(state.orders, 'count', payload.count)
  },
  PUSH_RAWMATERIAL_PURCHASE_ORDER (state, payload) {
    state.orders.unshift(payload)
  }
}



