import { HTTP_WITH_TOKEN } from 'HTTP'

export const getRawMaterials = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`raw-materials/organization/${params.organizationId}/?${params.filters}`)
}

export const postRawMaterial = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `raw-materials/organization/${params.organizationId}/`,
    params.params
  )
}

export const getRawMaterialPurchaseHistory = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(
    `orders/organization/${params.organizationId}/raw-materials/?${params.filters}`
  )
}

export const postRawMaterialPurchase = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `orders/organization/${params.organizationId}/raw-materials/`,
    params.params
  )
}



