import { getClients, postClient } from '../api'
import qs from 'qs'

export const fetchClients = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return getClients(params).then(response => {
    commit('SET_CLIENTS', response.data)
  })
}

export const addClient = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return postClient(params).then(response => {
    commit('PUSH_CLIENT', response.data)
  })
}



