import { HTTP_WITH_TOKEN } from 'HTTP'

export const getProducts = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`products/organization/${params.organizationId}/?${params.filters}`)
}

export const postProduct = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `products/organization/${params.organizationId}/`,
    params.params
  )
}



