import { getVendors, postVendor, putVendor } from '../api'
import qs from 'qs'

export const fetchVendors = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return getVendors(params).then(response => {
    commit('SET_VENDORS', response.data)
  })
}

export const addVendor = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return postVendor(params).then(response => {
    commit('PUSH_VENDOR', response.data)
  })
}

export const updateVendor = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return putVendor(params)
}



