import { HTTP, CLIENT, HTTP_WITH_TOKEN } from 'HTTP'
import qs from 'qs'

export const getOauthToken = (params) => {
  params['client_id'] = CLIENT.client_id
  params['client_secret'] = CLIENT.client_secret
  // params['scope'] = CLIENT.scope

  let parameters = qs.stringify(params)
  return HTTP.post(
    'accounts/login/', parameters
  )
}

export const postLogout = (params) => {
  params['client_id'] = CLIENT.client_id
  params['client_secret'] = CLIENT.client_secret
  let parameters = qs.stringify(params)
  return HTTP_WITH_TOKEN(params.token).post(
    'accounts/logout/',
    parameters
  )
}



