import { HTTP_WITH_TOKEN } from 'HTTP'

export const getSalesOrders = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`sales/organization/${params.organizationId}/sales/?${params.filters}`)
}

export const postSalesOrder = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `sales/organization/${params.organizationId}/sales/`,
    params.params
  )
}



