import { getProducts, postProduct } from '../api'
import qs from 'qs'

export const fetchProducts = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return getProducts(params).then(response => {
    commit('SET_PRODUCTS', response.data)
  }).catch(e => {
    console.log(e)
  })
}

export const addProduct = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return postProduct(params).then(response => {
    commit('PUSH_PRODUCT', response.data)
  }).catch(e => {
    commit('SET_ERROR', e.message)
  })
}



