import { ClientsView, ClientCreateForm, ClientsListView } from './components'

export default [
  {
    path: '/clients',
    components: {
      default: ClientsView
    },
    meta: { requiredAuth: true, title: 'Clients' },
    children: [
      {
        path: 'create',
        name: 'clients.create',
        component: ClientCreateForm,
        meta: { requiredAuth: true, title: 'Clients > Add' }
      },
      {
        path: 'list',
        name: 'clients.list',
        component: ClientsListView,
        meta: { requiredAuth: true, title: 'Clients > List' }
      },
      {
        path: '',
        redirect: 'list',
        name: 'clientsView'
      }
    ]
  }
]



