import { RawMaterialView, RawMaterialListView, RawMaterialCreateView, RawMaterialOrdersView } from './components'
export default [
  {
    path: '/raw-materials',
    components: {
      default: RawMaterialView
    },
    meta: { requiredAuth: true, title: 'Raw Materials' },
    children: [
      {
        path: 'list',
        component: RawMaterialListView,
        name: 'rawMaterials.listView',
        meta: { requiredAuth: true, title: 'Raw Materials' }
      },
      {
        path: 'create',
        name: 'rawMaterials.createView',
        component: RawMaterialCreateView,
        meta: { requiredAuth: true, title: 'Raw Material > Add' }
      },
      {
        path: 'orders',
        name: 'rawMaterials.ordersView',
        component: RawMaterialOrdersView,
        meta: { requiredAuth: true, title: 'Raw Material > Orders' }
      },
      {
        path: '',
        redirect: 'listView',
        name: 'rawMaterialsView'
      }
    ]
  }
]



