import { HTTP_WITH_TOKEN } from 'HTTP'

export const getProcurements = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`sales/organization/${params.organizationId}/procurements/?${params.filters}`)
}

export const postProcurement = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `sales/organization/${params.organizationId}/procurements/`,
    params.params
  )
}



