import { getOauthToken, postLogout } from '../api'
import {LocalStorage} from 'SERVICES'

const COOKIE_NAME = 'organizstionUser'

export const userLogin = ({commit}, params) => {
  params['grant_type'] = 'password'
  return getOauthToken(params).then(response => {
    return LocalStorage.set(COOKIE_NAME, response.data)
  }).then((data) => {
    commit('UPDATE_SESSION', data)
  })
}

export const loadUserFromStorage = ({commit}) => {
  let data = LocalStorage.get(COOKIE_NAME)
  if (data) {
    commit('UPDATE_SESSION', data)
  }
}

export const refreshOauthToken = ({dispatch, commit, getters}) => {
  let now = new Date()
  let refreshToken = getters.getRefreshToken
  let seconds = (getters.getExpireTime - now) / 1000
  if (seconds < 20 && refreshToken) {
    let params = {
      grant_type: 'refresh_token',
      'refresh_token': refreshToken
    }
    return getOauthToken(params).then(response => {
      commit('UPDATE_SESSION', response.data)
    }).catch(e => {
      dispatch('userLogout')
    })
  }
}

export const userLogout = ({commit, getters}) => {
  let params = {
    token: getters.getAccessToken
  }
  return postLogout(params).then(() => {
    LocalStorage.clear()
    commit('USER_LOGOUT')
  })
}

export const userErrorLogout = ({commit}, payload) => {
  commit('USER_LOGOUT')
  commit('AUTH_ERROR', payload)
}

export const resetAuthError = ({commit}) => {
  commit('RESET_ERROR')
}

export const setAuthErrorMessage = ({commit}, message) => {
  commit('SET_ERROR_MESSAGE', message)
}



