import { HTTP_WITH_TOKEN } from 'HTTP'

export const getVendors = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`organizations/${params.organizationId}/vendors/?${params.filters}`)
}

export const postVendor = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `organizations/${params.organizationId}/vendors/`,
    params.params
  )
}

export const putVendor = (params) => {
  return HTTP_WITH_TOKEN(params.token).put(
    `organizations/${params.organizationId}/vendors/`,
    params.params
  )
}



