export default {
  UPDATE_SESSION (state, payload) {
    let expireAt = new Date()
    // Set expire time 10 seconds before server time
    expireAt.setSeconds(payload.expires_in)
    state.accessToken = payload.access_token
    state.refreshToken = payload.refresh_token
    state.expiresIn = payload.expires_in
    state.tokenType = payload.token_type
    state.scope = payload.scope
    state.createdAt = payload.created_at
    state.authenticated = true
    state.expireAt = expireAt
    state.error = null
  },

  AUTH_ERROR (state, payload) {
    state.authenticated = false
    state.refreshToken = undefined
    state.token = undefined
    state.error = payload.data.error
  },

  USER_LOGOUT (state) {
    state.authenticated = false
    state.refreshToken = undefined
    state.token = undefined
    state.error = null
  },
  RESET_ERROR (state) {
    state.error = null
  },
  SET_ERROR_MESSAGE (state, message) {
    state.authenticated = false
    state.refreshToken = undefined
    state.token = undefined
    state.error = message
  }
}



