import { getOrganizations } from '../api'
import {LocalStorage} from 'SERVICES'

const COOKIE_NAME = 'organizstionDetails'

export const fetchOrganizations = ({commit, getters}) => {
  let params = {
    token: getters.getAccessToken
  }
  return getOrganizations(params).then(response => {
    return LocalStorage.set(COOKIE_NAME, response.data)
  }).then(data => {
    commit('SET_ORGANIZATION', data)
  }).catch(e => {
    commit('SET_ORGANIZATION_ERROR', e)
  })
}

export const loadOrganizationsFromStorage = ({commit}) => {
  let data = LocalStorage.get(COOKIE_NAME)
  if (data) {
    commit('SET_ORGANIZATION', data)
  }
}



