import axios from 'axios'
// import store from '../store'

// Client details
export const CLIENT = process.env.CLIENT

export const BASE_URL = process.env.API_BASE_URL
// HTTP object to consume API's
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
export const HTTP = axios.create({
  baseURL: BASE_URL
  // headers: {
  //   Authorization: LocalStorage.getBearerToken()
  // }
})

export const HTTP_WITH_TOKEN = (token) => {
  return axios.create({
    baseURL: BASE_URL,
    // timeout: 1000,
    headers: {
      Authorization: 'Bearer ' + token
    }
  })
}

axios.interceptors.response.use((response) => { // intercept the global error
  return response
}, function (error) {
  console.log(error)
  // if (store && error) {
  //   store.dispatch('loadUserFromStorage').then(() => {
  //     return store.dispatch('refreshOauthToken')
  //   })
  // }
})



