import * as actions from './actions'
import getters from './getters'
import mutations from './mutations'

const state = {
  list: {
    data: [],
    count: 0
  },
  current: {
    id: undefined,
    name: undefined,
    address_line_1: undefined,
    address_line_2: undefined,
    email: undefined,
    contact_number: undefined,
    logo: undefined
  },
  error: ''
}

export default {
  state,
  actions,
  getters,
  mutations
}



