import { HTTP_WITH_TOKEN } from 'HTTP'

export const getProductions = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`raw-materials/organization/${params.organizationId}/productions/?${params.filters}`)
}

export const postProduction = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `raw-materials/organization/${params.organizationId}/productions/`,
    params.params
  )
}



