import { ProductsView, ProductListView, ProductCreateView } from './components'

export default [
  {
    path: '/products',
    components: {
      default: ProductsView
    },
    meta: { requiredAuth: true, title: 'Products' },
    children: [
      {
        path: 'list',
        component: ProductListView,
        name: 'products.listView',
        meta: { requiredAuth: true, title: 'Products > List' }
      },
      {
        path: 'create',
        name: 'products.createView',
        component: ProductCreateView,
        meta: { requiredAuth: true, title: 'Products > Add' }
      },
      {
        path: '',
        redirect: 'listView',
        name: 'products.view'
      }
    ]
  }
]



