export default {
  getOrganizations (state, getters) {
    return state.list
  },
  getOrganization (state) {
    return state.current
  },
  getOrganizationName (state, getters) {
    return getters.getOrganization.name
  },
  getOrganizationID (state, getters) {
    return getters.getOrganization.id
  }
}



