import * as Api from '../api'
import qs from 'qs'

export const fetchRawMaterials = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return Api.getRawMaterials(params).then(response => {
    commit('SET_RAWMATERIALS', response.data)
  }).catch(e => {
    console.log(e)
  })
}

export const addRawMaterial = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return Api.postRawMaterial(params).then(response => {
    commit('PUSH_RAWMATERIAL', response.data)
  }).catch(e => {
    commit('SET_ERROR', e.message)
  })
}

export const fetchRawMaterialPurchaseOrders = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return Api.getRawMaterialPurchaseHistory(params).then(response => {
    commit('SET_PURCHASE_ORDERS', response.data)
  }).catch(e => {
    console.log(e)
  })
}

export const createRawMaterialPurchaseOrder = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return Api.postRawMaterialPurchase(params).then(response => {
    commit('PUSH_RAWMATERIAL_PURCHASE_ORDER', response.data)
  }).catch(e => {
    commit('SET_ERROR', e.message)
  })
}



