import { getSalesOrders, postSalesOrder } from '../api'
import qs from 'qs'

export const fetchSalesOrders = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return getSalesOrders(params).then(response => {
    commit('SET_SALES_ORDERS', response.data)
  })
}

export const createSalesOrder = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return postSalesOrder(params).then(response => {
    commit('PUSH_SALES_ORDER', response.data)
  })
}



