import { postBank } from '../api'

export const addBank = ({commit, getters}, account) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: account.values,
    type: account.type,
    id: account.id
  }
  return postBank(params).then(response => {
    commit('ADD_BANK', response.data)
  })
}

export const setBankList = ({commit}, list) => {
  commit('SET_BANK_LIST', list)
}



