import * as actions from './actions'
import getters from './getters'
import mutations from './mutations'

const state = {
  'accessToken': undefined,
  'refreshToken': undefined,
  'expiresIn': '',
  'tokenType': '',
  'scope': '',
  'createdAt': '',
  'authenticated': false,
  'error': null,
  'expireAt': new Date()
}

export default {
  state,
  actions,
  getters,
  mutations
}



