import { HTTP_WITH_TOKEN } from 'HTTP'

export const getClients = (params) => {
  return HTTP_WITH_TOKEN(params.token).get(`organizations/${params.organizationId}/clients/?${params.filters}`)
}

export const postClient = (params) => {
  return HTTP_WITH_TOKEN(params.token).post(
    `organizations/${params.organizationId}/clients/`,
    params.params
  )
}



