import Vue from 'vue'

export default {
  SET_PRODUCTS (state, payload) {
    Vue.set(state.list, 'data', payload.results)
    Vue.set(state.list, 'count', payload.count)
    state.current = payload[0]
  },
  PUSH_PRODUCT (state, payload) {
    state.list.push(payload)
  },
  SET_ERROR (state, error) {
    state.error = error
  }
}



