import { routes as users } from './users'
import { routes as organizations } from './organizations'
import { routes as vendors } from './vendors'
import { routes as rawMaterials } from './raw_materials'
import { routes as products } from './products'
import { routes as sales } from './sales'
import { routes as procurements } from './procurements'
import { routes as clients } from './clients'
import { routes as banks } from './banks'

import { PageNotFound } from 'LAYOUTS_COMPONENTS'

let routes = [
  {
    path: '',
    redirect: 'raw-materials/list'
  },
  {
    path: '/',
    redirect: 'raw-materials/list'
  },
  {
    path: '/404',
    component: PageNotFound,
    name: 'pageNotFound'
  },
  {
    path: '*',
    redirect: '/404'
  }
]
export default [
  ...routes, ...users, ...organizations, ...vendors, ...rawMaterials,
  ...products, ...sales, ...procurements, ...clients, ...banks
]



