import * as Api from '../api'
import qs from 'qs'

export const fetchProductions = ({commit, getters}, filters) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    filters: qs.stringify(filters)
  }
  return Api.getProductions(params).then(response => {
    commit('SET_PRODUCTIONS', response.data)
  })
}

export const createProduction = ({commit, getters}, values) => {
  let params = {
    token: getters.getAccessToken,
    organizationId: getters.getOrganizationID,
    params: values
  }

  return Api.postProduction(params).then(response => {
    commit('PUSH_PRODUCTION', response.data)
  })
}



